import db from "./firebase";
import { useEffect, useState } from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { Switch } from "@mui/material";

const DataFetch = () => {
  //default app states
  const [allSubscripions, setAllSubscription] = useState([]);
  const [dbName, setDbName] = useState("hackamej22");

  // initialize component with function start
  useEffect(() => {
    getDataToDash();
  }, [dbName]);

  // allows get into firestoredatabase
  const getDataToDash = async () => {
    const response = db.collection(dbName);
    const data = await response.get();
    const allCollectionItems = data.docs.map((item) => item.data());
    setAllSubscription(allCollectionItems);
  };
  // const renderOtherXps = (extention) => {
  //   return extention === true ? "Sim" : "Não";
  // };
  console.log(allSubscripions);
  return (
    <>
      <TableContainer component={Paper}>
        <Table sx={{ width: "auto" }} size="small" aria-label="a dense table">
          <TableHead>
            <TableRow>
              <TableCell>Nome</TableCell>
              <TableCell>Email</TableCell>
              <TableCell>Telefone Contato</TableCell>
              <TableCell>Empresa Jr</TableCell>
              <TableCell>Função na EJ</TableCell>
              <TableCell>CEP</TableCell>
              <TableCell>RUA</TableCell>
              <TableCell>Bairro</TableCell>
              <TableCell>Cidade</TableCell>
              <TableCell>Numero</TableCell>
              <TableCell>Complemento</TableCell>
              <TableCell>Tamanho da Camisa</TableCell>
              <TableCell>Hackathon</TableCell>
              <TableCell>StartUpWeekend</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {allSubscripions.map((row) => (
              <TableRow
                key={row.name}
                sx={{ "&:last-child td, &:last-child th": { border: 0 } }}>
                <TableCell component="th" scope="row">
                  {row.dataForms.userName}
                </TableCell>
                <TableCell>{row.dataForms.email}</TableCell>
                <TableCell>{row.dataForms.telefone}</TableCell>
                <TableCell>{row.dataForms.ej}</TableCell>
                <TableCell>{row.dataForms.funcaoNaEJ}</TableCell>
                <TableCell>{row.dataForms.cep}</TableCell>
                <TableCell>{row.dataForms.addressData.logradouro}</TableCell>
                <TableCell>{row.dataForms.addressData.bairro}</TableCell>
                <TableCell>{row.dataForms.addressData.localidade}</TableCell>
                <TableCell>{row.dataForms.numero}</TableCell>
                <TableCell>{row.dataForms.endereco.complemento}</TableCell>
                <TableCell>{row.dataForms.tamanhoCamisa}</TableCell>
                <TableCell>{row.dataForms.hackathon ? "Sim" : "não"}</TableCell>
                <TableCell>
                  {row.dataForms.startupWknd ? "Sim" : "Não"}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
};

export default DataFetch;
